import { Button, Form as AntForm, message } from "antd";
import { FastField, Formik } from "formik";
import React from "react";
import { useDispatch } from "react-redux";
import * as Yup from "yup";
import { getListProduct } from "../../actions/productAction";
import { addNewType, updateType } from "../../actions/typeAction";
import { AntInput } from "../../customField/CreateAntField";

const FormTypeEdit = ({ typeCurrent, setTypeCurrent, listType }) => {
    const [form] = AntForm.useForm();
    const dispatch = useDispatch();
    const initialValues = typeCurrent;
    const validationSchema = Yup.object().shape({
        name: Yup.string()
            .required("Tn khng c b trng.")
            .matches(
                /^[a-zA-Z_" +\\s]+$/,
                "Khng c c s."
            ),
    });

    // TODO Thm, cp nht loi mn

    const onSubmit = (values, formAction) => {
        setTypeCurrent(null);
        if (typeCurrent.name) {
            const newListType = listType.filter((item) => item._id !== typeCurrent._id);
            const typeExits = newListType.find(
                (item) => item.name.toLowerCase() === values.name.toLowerCase()
            );
            if (typeExits) {
                formAction.setSubmitting(false);
                return message.error({
                    content: `Sn phm  tn ti`,
                    style: {
                        position: "relative",
                        top: 10,
                        right: "-80vh",
                    },
                });
            }
            dispatch(updateType(typeCurrent._id, values));
            message.success({
                content: `Cp nht thnh cng`,
                style: {
                    position: "relative",
                    top: 10,
                    right: "-80vh",
                },
            });
        }

        if (!typeCurrent.name) {
            const typeExits = listType.find(
                (item) => item.name.toLowerCase() === values.name.toLowerCase()
            );
            if (typeExits) {
                formAction.setSubmitting(false);
                return message.error({
                    content: `Sn phm  tn ti`,
                    style: {
                        position: "relative",
                        top: 10,
                        right: "-80vh",
                    },
                });
            }
            dispatch(addNewType(values));
            message.success({
                content: `Thm thnh cng`,
                style: {
                    position: "relative",
                    top: 10,
                    right: "-80vh",
                },
            });
        }

        dispatch(getListProduct());
        formAction.resetForm();
        formAction.setSubmitting(false);
    };

    return (
        <div className="form_type">
            <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={onSubmit}
                enableReinitialize={true}
            >
                {(formikProps) => {
                    const { handleSubmit, isValid, isSubmitting } = formikProps;
                    return (
                        <AntForm form={form} encType="multipart/form-data">
                            <FastField
                                name="name"
                                label="Tn loi"
                                type="text"
                                placeholder="Nhp tn loi"
                                hasFeedback={true}
                                component={AntInput}
                            />
                            <Button
                                onClick={handleSubmit}
                                disabled={!isValid || isSubmitting}
                                loading={isSubmitting}
                                type="primary"
                            >
                                {typeCurrent.name !== "" ? "Cp nht" : "Thm"}
                            </Button>
                        </AntForm>
                    );
                }}
            </Formik>
        </div>
    );
};

export default FormTypeEdit;
